
nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #E04818 ;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
    height: 50px;
    border-radius: 5px;
    justify-content: center;
}

#menu, #menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  #menu {
    margin: 20px auto;
    background-color: #E04818;
    border-radius: 5px;
  }
  
  #menu:before,
  #menu:after {
    content: '';
    display: table;
  }
  
  #menu:after {
    clear: both;
  }
  
  #menu li {
    float: left;
    position: relative;
  }
  
  #menu a {
    float: left;
    padding: 10px 30px;
    text-transform: uppercase;
    font-family: 'Baloo 2', cursive;
    text-decoration: none;
    color: #fff;
  }
  
  #menu li:hover > a {
    color: #fafafa;
    border-radius: 10px;
    background: #262626;
  }
  
  #menu ul {
    margin: 10px 0 0 0;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 46px;
    left: 0;
    z-index: 1;
    background: #262626;
    border-radius: 3px;
    transition: all .3s ease-in-out;
  }
  
  #menu li:hover > ul {
    opacity: 1;
    visibility: visible;
    margin: 0;
  }
  
  #menu ul ul {
    top:0;
    left: 150px;
    margin: 0 0 0 20px;
  }
  
  #menu ul li {
    float: none;
    display: block;
    border: 0;
    box-shadow: 0 1px 0 #111, 0 2px 0 #262626;
  }
  
  #menu ul li:last-child {
    box-shadow: none;
  }
  
  #menu ul a {
    padding: 10px;
    display: block;
    white-space: nowrap;
    float: none;
    text-transform: uppercase;
  }
  
  #menu ul a:hover {
    background-color: #E04818;
  }
  
  #menu ul li:first-child > a {
    border-radius: 3px 3px 0 0;
  }
  
  #menu ul li:first-child > a:after {
    content: '';
    position: absolute;
    left: 40px;
    top: -6px;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fff;
  } 

  #mobile i{
    color: black;
  }

  #mobile {
    display: none;
  }

 @media screen and (max-width:769px) {
  #menu {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
  }
  
  #menu.active {
    right: 0px;
  }

  #menu li {
    border-bottom: 1px;
  }

  #mobile {
    display: block;
  }

  #mobile i {
    font-size: 24px;
    cursor: pointer;
  }
  
 }