.friend-list {
    max-width: 500px;
    margin: 100px auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 2px 2px 10px black;
}

.friend-list h2 {
    text-align: center;
}

button {
    border: 1px solid black;
    outline: none;
    width: 20%;
    height: 10%;
    padding: .4em;
    margin-left: 1em;
    margin: 5px;
}

button:hover {
    background-color: lightblue;
}


.friend {
    text-align: center;
    border-bottom: 1px solid black;
    margin: 10px;
}
