
.powerups {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    width: fit-content;
}

.game-btn {
    justify-content: center;
    border-radius:5px;
}

.powerup {
    border: 1px solid grey;
    width: 5rem;
    height: 5rem;
    text-align: center;
    font-size: 3em;
    font-family: 'Calibri';
    font-weight: bold;
    color: black;
    line-height: 5rem;
}

.wobist {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 50%;
    height: 50%;
    margin: auto;
    border: 3px solid black;

}